// import React from "react";
import Lazyman, { lazyDocument } from "./lazy/index";
import { Lazyable, State, Stateable } from "./lazy/Lazyable";
import HTMLDOMDrive from "./lazy/LazyDom";
import { generateArray } from "./lazy/utils/Array";
import { useLazyable } from "./lazy/VirtualElement";
import { Component } from "./lazy/Component";

function A() {
  const d1 = useLazyable({ count: 1 });
  const d2 = useLazyable({ age: 1 });
  return (
    <div onClick={() => d1.count++}>
      {d1.count}
      <div
        onClick={(e: any) => {
          e.stopPropagation();
          d2.age++;
        }}
      >
        {d2.age}
        {{ a: 12, b: "钱志祥" }}
      </div>
    </div>
  );
}

// @Stateable()
class B extends Component {
  @State()
  age = 100;
  render() {
    console.log(this);
    return <div onClick={() => this.age++}>{this.age}</div>;
  }
}

const data = Lazyable({
  ref: undefined,
  count: 1,
  arr: generateArray(4, (i) => i),
} as { count: number; arr: number[]; ref: any });
Lazyman.drive(HTMLDOMDrive);
Lazyman.render(
  <div>
    <A />
    <B />
    <B />
    <div>
      <button
        onClick={() => {
          data.arr.push(data.arr.length);
        }}
      >
        增加新的值
      </button>
      <button
        onClick={() => {
          data.arr.pop();
        }}
      >
        减少新的值
      </button>
    </div>
    <div>
      {data.arr.map((i, index) => (
        <div
          key={index}
          onClick={() => {
            data.arr[index]++;
          }}
        >
          {i}
        </div>
      ))}
    </div>
    {data.arr}
  </div>,
  lazyDocument.querySelector("#root")!
);
